.main-nav {
  margin-left: auto;
  padding: 0;
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 64px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  background-color: #e6e6e6;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list-item {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
  left: auto;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 31px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__wrapper {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.site-list a {
  position: relative;
  display: block;
  padding: 24px 21px;
  border-bottom: 1px solid #e6e6e6;
  text-align: center;
}

.site-list .main-nav__list-item:first-child {
  border-top: 1px solid #e6e6e6;
}

.main-nav__list-item {
  list-style-type: none;
}

.main-nav__list-link {
  text-decoration: none;
  display: block;
  color: #000000;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 66px;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: #ffffff;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .main-nav__list-item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }

  .main-nav--nojs .main-nav__list-item {
    display: block;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav {
    margin-left: auto;
  }

  .main-nav__list {
    display: flex;
  }

  .main-nav__list-item--active {
    position: relative;
  }
  .main-nav__list-item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 3px;
    background: #68b738;
  }

  .main-nav__list-item:not(:last-child) {
    margin-right: 0;
  }

  .main-nav__list-link {
    font-size: 18px;
  }

  .main-nav__toggle {
    display: none;
  }

  .site-list a {
    border-bottom: none;
  }

  .site-list .main-nav__list-item:first-child {
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    width: 100%;
  }

  .main-nav__list {
    margin-left: auto;
  }

  .main-nav__list-item::after {
    background: #ffffff;
  }

  .main-nav__list-link {
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }

  .catalog-nav__list-link {
    color: #000000;
  }

  .catalog-nav__list-item::after {
    background: #68b738;
  }

  .main-nav__toggle {
    display: none;
  }
}
