    height: 80px;
  }

  .advantages__item::after {
    font-family: "Oswald" "Verdana", sans-serif;
    width: 120px;
    content: counter(advantages);
    position: absolute;
    font-size: 280px;
    color: #f2f2f2;
    z-index: -1;
    top: 100px;
    right: 0;
    left: auto;
